BAAgB;EACpB;EACA;MACI,gBAAgB;EACpB;EACA;IACE,aAAa;EACf;AACF;AACA;EACE;IACE,iBAAiB;IACjB,qCAAqC;IACrC,SAAS;IACT,YAAY;EACd;EACA;MACI,iBAAiB;MACjB,qCAAqC;MACrC,SAAS;EACb;EACA;MACI,iBAAiB;EACrB;EACA;MACI,iBAAiB;EACrB;AACF","sourcesContent":["@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap');\n\n:root {\n  --first-color: #F9C057;\n  --second-color: #1E1512;\n  --third-color: #FFF2D9;\n}\n\n/* template */\n*{\n  box-sizing: border-box;\n}\nbody, html {\n  margin: 0;\n  padding: 0;\n  width: 100%;\n  height: 100%;\n  overflow-x: hidden;\n  scroll-behavior: smooth;\n}\nbody{\n  font-family: 'Poppins', sans-serif;\n}\nul{\n  list-style: none;\n}\na{\n  text-decoration: none;\n}\nbutton {\n  font-family: 'Poppins';\n}\nsection {\n  padding: 20px;\n}\nsection .title {\n  text-align: center;\n  font-size: 20px;\n  font-weight: 600;\n  color: var(--second-color);\n}\ninput{\n  min-height: 44px;\n}\n/* skip-link */\n.skip-link {\n  position: absolute;\n  top: -50px;\n  left: 0;\n  background-color: var(--second-color);\n  color: white;\n  padding: 8px;\n  z-index: 100;\n}\n.skip-link:focus {\n  top: 0;\n}\n\n/* header */\n.cap {\n  background-color: var(--first-color);\n  width: 100%;\n  height: 10px;\n}\n.header__inner {\n  position: relative;\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  padding: 10px 30px;\n}\n.header__menu {\n  position: absolute;\n  right: 30px;\n  background-color: transparent;\n  border: none;\n  font-size: 30px;\n  font-weight: 800;\n  color: var(--second-color);\n  cursor: pointer;\n}\n.header__logo{\n  font-weight: 600;\n  color: var(--second-color);\n}\n\n/* nav */\n.nav__list {\n  z-index: 10;\n}\n.nav__list.open {\n  transform: translate(0, 0);\n}\n\n/* hero */\n.hero__inner {\n  width: 100%;\n}\n.background__hero{\n  position: relative;\n  max-height: 480px;\n  overflow: hidden;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n 